import { getActClass } from './utils';


export default {
  name: 'PressActSelectAward',
  options: {
    virtualHost: true,
    styleIsolation: 'shared',
  },
  props: {
    show: {
      type: Boolean,
      default: false,
      required: false,
    },
    list: {
      type: Array,
      default() {
        return [];
      },
      required: true,
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    'clickButton',
    'clickCloseButton',
    'update:show',
  ],
  data() {
    return {
      clickIndexFooter: 0,
    };
  },
  methods: {
    closeDialog() {
      this.$emit('update:show', false);
      this.$emit('clickCloseButton');
    },
    clickButton() {
      this.$emit('update:show', false);
      this.$emit('clickButton', this.list[this.clickIndexFooter]);
    },
    // 点击选中物品显示高亮边框和奖励名称, 第一个物品默认选中
    clickHandle(index) {
      if (this.clickIndexFooter === index) {
        this.clickIndexFooter = null;
      } else {
        this.clickIndexFooter = index;
      }
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
